
<template>
  <div class="space-y-4">
    <div class="flex items-center justify-between">
      <h1 class="text-xl font-semibold">My Lookups</h1>
      <NuxtLink class="btn" to="/lookups/new">New Lookup</NuxtLink>
    </div>
    <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
      <LookupCard v-for="it in list" :key="it.id" :item="it" />
    </div>
  </div>
</template>
<script setup lang="ts">
import LookupCard from '@/components/LookupCard.vue'
const { $get } = useApi()
const list = ref<any[]>([])
onMounted(async ()=>{
  const res:any = await $get('/vin-lookups')
  list.value = res.data || res
})
</script>
